import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';
import { PageMaintainAddBean } from '../bean/page-maintain.bean';
import { InterfaceHttpService } from '../../../http/interface.http.servive';
import { checkEnAndNum } from 'app/@uisftech/common/service/validator';

/**
 * 页面维护新增
 */
@Component({
    selector: 'page-maintain-add',
    templateUrl: './page-maintain-add.component.html',
    styleUrls: ['./page-maintain-add.component.scss'],
})

export class pageMaintainAddComponent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    pageMaintainAddBean: PageMaintainAddBean = new PageMaintainAddBean();
    msgs: any = [];//提示信息
    constructor(
        public interfaceHttpService: InterfaceHttpService
    ) {

    }

    ngOnInit() {

    }

    //保存
    save() {
        if (!this.pageMaintainAddBean.pageId) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入页面编号！' });
            return
        }
        if (!this.pageMaintainAddBean.pageName) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入页面名称！' });
            return
        }
        this.interfaceHttpService.createPageDefinition(this.pageMaintainAddBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: "新增成功" });
                this.outValue.emit(false)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //重置
    reset() {
        this.pageMaintainAddBean = new PageMaintainAddBean();
    }

    //取消
    cancel() {
        this.outValue.emit(false)
    }
    //校验只能输入数字字母
    onKeyupNumber(event) {
        if (event.target.value != '') {
            if (checkEnAndNum(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字和字母!' }];
                event.target.value = '';
                return
            }
        }
    }


}