import { Component, OnInit, Output, EventEmitter,Input } from '@angular/core';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';
import { tableMessage } from '../../../../../../../@uisftech/common/constant/config';
import { PageMaintainAddBean } from '../bean/page-maintain.bean';
import { InterfaceHttpService } from '../../../http/interface.http.servive';
/**
 * 页面维护详情
 */
@Component({
    selector: 'page-maintain-detail',
    templateUrl: './page-maintain-detail.component.html',
    styleUrls: ['./page-maintain-detail.component.scss'],
})

export class pageMaintainDetailComponent implements OnInit {
    @Input('in-value') inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    pageMaintainAddBean: PageMaintainAddBean = new PageMaintainAddBean();
    msgs: any = [];//提示信息
    dataList:any;//表格数据
    ModuleList:any;//表格数据
    constructor(
        public interfaceHttpService: InterfaceHttpService
    ) {

    }

    ngOnInit() {
        this.pageMaintainAddBean.pageId = this.inValue.pageId;
        this.pageMaintainAddBean.pageName = this.inValue.pageName;
        this.pageMaintainAddBean.pageUrl = this.inValue.pageUrl;
        this.query()
    }

    //查询列表
    query() {
        this.interfaceHttpService.queryPageElementInfoById({pageId:this.inValue.pageId}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if(data.elementInfoList){
                    this.dataList = data.elementInfoList;
                }
                if(data.interfaceModule){
                    this.ModuleList = data.interfaceModule
                }
               
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //返回
    return() {
        this.outValue.emit(false)
    }
}