import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { tableMessage } from '../../../../../../@uisftech/common/constant/config';
import { InterfaceHttpService } from '../../http/interface.http.servive';
import { PageMaintainBean } from './bean/page-maintain.bean';


/**
 * 页面维护界面
 */
@Component({
    selector: 'page-maintain',
    templateUrl: './page-maintain.component.html',
    styleUrls: ['./page-maintain.component.scss'],
    providers: [ConfirmationService]
})
/**
 * 规则新增
 */
export class PageMaintainComponent implements OnInit {
    tableData: any = [];
    updateDisplay: boolean = false;
    detailDisplay: boolean = false;
    elementDisplay: boolean = false;
    showFlag: string;
    headerTitle: string;
    pageMaintainBean: PageMaintainBean = new PageMaintainBean();//主页面bean文件
    msgs: any = [];//提示信息
    //分页
    total: number;
    first: number = 0;
    inValue: any;//修改数据
    detailInvalue: any;//详情
    elementData:any;//元素配置

    constructor(
        public confirmationService: ConfirmationService,
        public interfaceHttpService: InterfaceHttpService
    ) { }

    ngOnInit() {
        this.pageMaintainBean.pageNum = 1;
        this.pageMaintainBean.pageSize = 10;
        this.queryClick();
    }

    //新增
    add() {
        this.showFlag = '1';
        this.headerTitle = '新增';
        this.updateDisplay = true;
    }

    //新增回调
    addCall(event) {
        this.updateDisplay = false;
        this.queryClick();
    }

    //查询
    queryClick() {
        this.interfaceHttpService.queryPageDefinitionList(this.pageMaintainBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.pageList;
                this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //重置
    reClean() {
        this.pageMaintainBean.pageName = '';
    }

    //元素配置
    deMaintain(item) {
        this.elementDisplay = true;
        this.elementData = item;
    }

    //元素定义回掉
    deployCall(event){
        this.elementDisplay = false;
    }

    //删除
    detele(item) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
            }
        });
    }

    //修改
    deupdate(param) {
        this.showFlag = '2';
        this.updateDisplay = true;
        this.headerTitle = '修改';
        this.inValue = param;
    }

    //修改回调
    updateCall(event) {
        this.updateDisplay = false;
        this.queryClick();
    }

    //详情
    deDetail(param) {
        this.detailDisplay = true;
        this.detailInvalue = param;
    }

    //详情回调
    detailCall(item) {
        this.detailDisplay = false;
    }

    //分页
    paginate(event) {
        this.pageMaintainBean.pageSize = event.rows;//每页显示的条数
        let currentPage = event.page + 1; //当前页
        this.pageMaintainBean.pageNum = currentPage;
        this.first = event.page * event.rows;
        this.queryClick();//调用查询的方法
    }
}