import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';
import { tableMessage } from '../../../../../../../@uisftech/common/constant/config';
import { InterfaceHttpService } from '../../../http/interface.http.servive';
import { ElementDeployBean } from '../bean/page-maintain.bean';
/**
 * 元素配置
 */
@Component({
    selector: 'element-deploy',
    templateUrl: './element-deploy.component.html',
    styleUrls: ['./element-deploy.component.scss'],
})

export class ElementDeployComponent implements OnInit {
    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    code: any;
    FiledType: any = [];
    FiledView: any = [];
    tableList1: any = [];
    tableList2: any = [];
    elelment: string;
    msgs: any = [];//提示信息
    arr:any = [];
    dataList:any =[];//表格数据
    ModuleList:any=[];//表格数据
    elementDeployBean:ElementDeployBean = new ElementDeployBean()
    AttrItemList:any;
    detailDisplay:boolean = false;
    checkValue:any = [];
    isModel:boolean = false;
    modelDetail:any;
    filedType:any = [
        {
            label:'字符串',value:'01'
        },{
            label:'数值',value:'02'
        },{
            label:'日期',value:'03'
        }
    ];
    filedView:any = [
        {
            label:'文本框',value:'01'
        },{
            label:'下拉列表框',value:'02'
        },{
            label:'日期型',value:'03'
        }
    ]
    elementType: any = [
        {
            label: '页面字段定义', value: '1',
        }, {
            label: '页面模板定义', value: '2',
        }
    ]
    constructor(
        public commfunc: Commfunc,
        public interfaceHttpService: InterfaceHttpService
    ) {
        this.codeValues();
        this.FiledType = this.code['FiledType'];//字段类型
        this.FiledView = this.code['filedView1'];//界面展示形式

    }

    ngOnInit() {
        this.elementDeployBean.pageName = this.inValue.pageName;
        this.elementDeployBean.pageId = this.inValue.pageId;
        this.elementDeployBean.pageUrl = this.inValue.pageUrl;
        this.elementDeployBean.daType = '1';
        this.query()
        
    }

    //查询列表
    query() {
        this.interfaceHttpService.queryPageElementInfoById({pageId:this.inValue.pageId}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if(data.elementInfoList){
                    this.dataList = data.elementInfoList;
                }
                if(data.interfaceModule){
                    this.ModuleList = data.interfaceModule
                }
               
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //查询模块列表
    queryModel() {
        this.interfaceHttpService.queryInterfaceModuleList({pageSize:100000,pageNum:1}).subscribe(data => {
            if (data.returnCode.code == 'success') {         
                this.modelDetail = data.InterfaceModuleList;             
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //查询字段列表
    queryAttrItemList() {
        this.interfaceHttpService.queryAttrItemList({pageSize:100000,pageNum:1}).subscribe(data => {
            if (data.returnCode.code == 'success') {         
                this.AttrItemList = data.fieldsList;             
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    
    //新增自定义
    addField() {
        this.checkValue = []
        if(!this.ModuleList){
            this.ModuleList = []
        }
        this.arr = this.ModuleList;
       this.detailDisplay = true;
       this.ModuleList.forEach(element => {
           this.checkValue.push(element.interfaceModuleId)
       });
       this.queryAttrItemList();
    }

    //页面元素保存
    detailDisplaySave(){
        this.detailDisplay = false;
        if(!this.dataList){
            this.dataList = [];
        }
        // this.dataList.push(...this.arr)
        this.dataList = _.clone(this.arr)
        console.log(this.dataList)
    }

    //模块选择出发事件
    selectModel(event,item) {
        if(event == true){
            this.arr.push(item)
        }
        if(event ==false) {
            this.arr.forEach((element,index) => {
                if(element.interfaceModuleId == item.interfaceModuleId){
                    this.arr.splice(index,1)
                }
            });
        }
    }

    //页面元素选择出发事件
    select(event,item) {
        if(event == true){
            this.arr.push(item)
        }
        if(event ==false) {
            this.arr.forEach((element,index) => {
                if(element.daField == item.daField){
                    this.arr.splice(index,1)
                }
            });
        }
    }

    //选择模块
    modelDetailSave(){
        this.isModel = false;
        if(!this.ModuleList){
            this.ModuleList = [];
        }
        this.ModuleList = _.clone(this.arr)
    }

    //删除
    deleteField(i) {
        this.dataList.splice(i, 1);
        this.dataList = _.clone(this.dataList);
    }

    //新增模块定义
    addieldTemplate() {
        this.checkValue = []
        if(!this.dataList){
            this.dataList = []
        }
        this.arr = this.dataList;
        this.isModel = true;
       this.dataList.forEach(element => {
           this.checkValue.push(element.daField)
       });
       this.queryModel();
        
    }

    //删除
    deleteTemplate(i) {
        this.ModuleList.splice(i, 1);
        this.ModuleList = _.clone(this.ModuleList);
    }

    //保存
    save() {
        if ((!this.dataList) || (this.dataList.length == '0')) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请配置页面字段！' });
            return
          }
        let arr = []
        this.dataList.forEach(element => {
            let obj ={};
            obj['daField'] = element.daField
            arr.push(obj)
        });
        let param = {
            pageId:this.elementDeployBean.pageId,
            daType:this.elementDeployBean.daType,
            elementList:arr
        }
        this.interfaceHttpService.dualPageElementAssoc(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message })
                this.outValue.emit(false)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //取消
    cancel() {
        this.outValue.emit(false)
    }

    //重置
    reset(){
        this.dataList = _.clone([])
    }
}