import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { PageMaintainComponent } from './page-maintain/page-maintain.component';


export const routes: Routes = [
    {
        path: '',
        children: [
            {
                path: 'page-maintain',
                component: PageMaintainComponent,
            },
          
        ]
    }
]
export const PagePreserveRouting: ModuleWithProviders = RouterModule.forChild(routes);